.playlistContentContainer {
    display: grid;
    position: relative;
    grid-template-columns: 1fr;
    grid-template-rows: max-content 1fr;
    grid-template-areas: 
    "header"
    "allVideos";
    justify-items: center;
    height: 100%;
    padding-top: 1rem;

    header {
        grid-area: header;
        width: 90%;
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
        justify-content: end;
        gap: 0.3rem;
        border-bottom: 2px solid $dark_green;
        padding-bottom: 1rem;

        h1 {
            font-weight: 600;
            font-size: 2.5rem;
            color: $velvet;
            text-transform: uppercase;
        }

        .addVideo {
            position: relative;
            display: flex;
            flex-flow: row nowrap;
            height: 2rem;
            opacity: 0.8;

            input {
                text-align: center;
                outline: none;
                border: none;
                background-color: black;
                border: 2px solid rgba($color: #ffffff, $alpha: 0.8);
                border-radius: 0.8rem;
                width: 15rem;
                height: 100%;
                z-index: 20;
                color: white;

                &::placeholder {
                    color: rgba($color: #ffffff, $alpha: 0.6);
                }
            }

            button {
                width: 3rem;
                height: 100%;
                margin-left: -1rem;
                padding-left: 1.2rem;
                z-index: 10;
                font-size: 1.5rem;
                border-top-right-radius: 0.8rem;
                border-bottom-right-radius: 0.8rem;
                background-color: $light_green;
                border: 2px solid rgba($color: #ffffff, $alpha: 0.8);
                color: white;
                cursor: pointer;

                &:hover {
                    opacity: 0.6;
                }
            }
        }

    }

    .allVideos {
        grid-area: allVideos;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: flex-start;
        gap: 3rem;
        overflow-y: auto;
        padding: 3rem;
        width: 100%;
        height: 100%;

        .singleVideo {
            position: relative;
            display: flex;
            flex-flow: column nowrap;
            align-items: center;
            width: max-content;
            height: max-content;
            gap: 0.5rem;

            .videoRemovalBtn {
                position: absolute;
                right: -0.6rem;
                top: -0.6rem;
                width: 2rem;
                height: 2rem;
                cursor: pointer;
                background-color: white;
                border: 3px solid black;
                border-radius: 50%;
                color: $maroon;
                z-index: 10;
                display: flex;
                justify-content: center;
                align-items: center;

                &:hover {
                    background-color: $maroon;
                    color: black;
                }
            }
            
            .thumbnailContainer {
                border: 4px solid rgba($color: #ffffff, $alpha: 0.9);
                border-radius: 1rem;
                position: relative;
                overflow: hidden;
                cursor: pointer;
                
                &::after {
                    content: "Play Video";
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 100%;
                    height: 100%;
                    color: transparent;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    pointer-events: none;
                    font-size: 1rem;
                    font-weight: 600;
                    transition: all 0.15s ease-in;
                }

                &:hover::after {
                    background-color: rgba($color: #000000, $alpha: 0.7);
                    color: white;
                    transition: all 0.15s ease-out;
                }
                
                img {
                    width: 20rem;
                    height: 12rem;
                    object-fit: cover;
                }
            }
            

            a {
                text-decoration: none;
                color: rgb(0, 179, 255);

                &:hover {
                    opacity: 0.6;
                }
            }
        }

        @include custom_scrollbar();
    }
}

@media screen and (max-width: 768px) {
    .playlistContentContainer {
        padding-top: 2.5rem;

        .allVideos {
            position: absolute;
            top: 0;
            bottom: 0;
            flex-flow: column nowrap;
            justify-content: flex-start;
            align-items: center;
        }
    }
}

@media screen and (max-width: 401px) {
    .playlistContentContainer {
        .allVideos {
            .singleVideo {
                .thumbnailContainer {
                    img {
                        width: 16rem;
                        height: 9rem;
                    }
                }
            }
        }
    }
}